<template>
  <div class="text-left">
    <v-toolbar flat>
      <v-toolbar-title class="mr-6">
        <span>{{ toolbarTitle }}</span>
        <header-selector :grid-config-change="fetchSetGridHeaders" :fieldKeys="itemKeys" :tableName="tableName" />
        <tooltip-icon-button
          btnColor="default"
          btnIcon="mdi-sync"
          :btnTooltip="refreshToolTipTitle"
          buttonSize="medium"
          :onClick="() => refreshGrid()"
        >
        </tooltip-icon-button
      ></v-toolbar-title>
      <grid-items-counter :num="selectedItems.length"></grid-items-counter>
      <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details></v-text-field>
    </v-toolbar>
    <v-expansion-panels dense v-model="panel" multiple>
      <v-expansion-panel dense v-for="(network, index) in networks" :key="index">
        <v-expansion-panel-header dense hide-actions>
          <template v-slot:default="{ open }">
            <b>NETWORK: {{ network.name }}</b>
            <v-icon style="text-align: end; display: block">
              {{ open ? 'mdi-chevron-down' : 'mdi-chevron-right' }}
            </v-icon>
          </template>
        </v-expansion-panel-header>
        <v-expansion-panel-content dense>
          <v-data-table
            class="text-left elevation-1"
            dense
            :headers="headers"
            :search="search"
            :items="network.items"
            v-model="selectedItems[network.uuid]"
            item-key="name"
            sort-by="name"
            group-by="network_device_key"
            show-select
            @toggle-select-all="val => onSelectAll(val, network.uuid)"
          >
            <template v-slot:item.name="{ item }">
              <text-button
                :text="`${item.name}`"
                :onClick="() => redirectTo(viewNames.points, network.uuid, item.device_uuid)"
              ></text-button>
            </template>
            <template v-slot:group.header="{ headers, items, toggle, group, isOpen }">
              <td :colspan="headers.length">
                <div class="network-group-wrapper">
                  <v-checkbox
                    v-if="networkDevicesModel[group]"
                    class="mt-0"
                    v-model="networkDevicesModel[group].isSelected"
                    color="nubeAqua darken-3"
                    @click="value => onDeviceSelection(group, index)"
                    :value="networkDevicesModel[group].isSelected"
                    hide-details
                    @change="check($event, items)"
                  ></v-checkbox>
                  <v-btn @click="toggle" x-small icon>
                    <v-icon>{{ isOpen ? 'mdi-chevron-double-down' : 'mdi-chevron-double-right' }}</v-icon>
                  </v-btn>
                  <div class="subtitle-2" v-if="networkDevicesModel[group]">
                    <text-button
                      :text="`Device: ${networkDevicesModel[group].device_name}`"
                      :onClick="() => redirectToDevice(viewNames.devices, group)"
                    ></text-button>
                  </div>
                </div>
              </td>
            </template>
          </v-data-table>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
  </div>
</template>
<script>
import ROUTES from '../../../constants/routes';
import TextButton from '../../Common/TextButton.vue';
import GridItemsCounter from '../../Common/gridItemsCounter.vue';
import TooltipIconButton from '../../RubixApps/TooltipIconButton.vue';
import { rfNetworkService } from '../../../services/rfNetworkService';
import HeaderSelector from '../../HeaderSelector/headerSelector.vue';
import * as headerBuilderService from '../../../services/headerBuilderService';
import * as gridTableHeadersService from '../../../services/gridTableHeadersService';
import * as gridNames from '../../../constants/gridNames';

const DELIMITER = ':=';

const viewNames = {
  devices: 'devices',
  points: 'points',
};
const redirectMap = {
  [viewNames.devices]: networkId => ROUTES.rfDevices.replace(':networkId', networkId),
  [viewNames.points]: (networkId, deviceId) =>
    ROUTES.rfPoints.replace(':networkId', networkId).replace(':deviceId', deviceId),
};

export default {
  name: 'rf-all-network-devices-grid',
  components: { GridItemsCounter, TooltipIconButton, TextButton, HeaderSelector },
  data() {
    return {
      selectedItems: {},
      panel: null,
      tableName: gridNames.RF_ALL_NETWORK_DEVICES_POINTS_GRID,
      viewNames: viewNames,
      search: '',
      items: [],
      networks: [],
      networkDevicesModel: {},
      toolbarTitle: 'All Points',
      headers: [],
    };
  },
  computed: {
    refreshToolTipTitle() {
      return `Refresh ${this.toolbarTitle}`;
    },
    itemKeys() {
      return (
        (Array.isArray(this.networks) &&
          this.networks.length > 0 &&
          Array.isArray(this.networks[0].items) &&
          this.networks[0].items.length > 0 &&
          Object.keys(this.networks[0].items[0])) ||
        []
      );
    },
  },
  mounted() {
    this.fetchSetGridHeaders();
    this.fetchNetworkDevices();
  },
  methods: {
    refreshGrid() {
      this.search = '';
      this.networks = [];
      this.selectedItems = {};
      this.fetchNetworkDevices();
    },
    fetchNetworkDevices() {
      this.isFetchingData = true;
      rfNetworkService
        .fetch({ with_points: true })
        .then(data => {
          this.networks = data;
          this.createSelectedItemsModel(this.networks);
          this.createNetworkDevicesModel(this.networks);
          this.setItems(this.networks);
        })
        .finally(() => {
          this.isFetchingData = false;
        });
    },
    setItems() {
      this.networks = (this.networks || []).map(network => {
        const items = (network.devices || []).reduce((acc, device) => {
          if (!Array.isArray(device.points)) {
            return acc;
          }

          const formattedPoints = (device.points || []).map(point => ({
            ...point,
            ...Object.keys(device).reduce((acc, deviceKey) => {
              const newKey = 'device_' + deviceKey;
              acc[newKey] = device[deviceKey];
              return acc;
            }, {}),
            network_device_key: this.getNetworkDeviceKey(network.uuid, device.uuid),
          }));
          acc.push(...formattedPoints);
          return acc;
        }, []);

        delete network.devices;

        return {
          ...network,
          items: items,
        };
      });
    },
    createSelectedItemsModel() {
      this.selectedItems = (this.networks || []).reduce((acc, network) => {
        acc[network.uuid] = [];
        return acc;
      }, {});
    },
    getNetworkDeviceKey(networkId, deviceId) {
      return `${networkId}${DELIMITER}${deviceId}`;
    },
    createNetworkDevicesModel(networks) {
      this.networkDevicesModel = {};

      networks.forEach(network => {
        network.devices.forEach(device => {
          this.networkDevicesModel[this.getNetworkDeviceKey(network.uuid, device.uuid)] = {
            network_uuid: network.uuid,
            device_uuid: device.uuid,
            network_name: network.name,
            device_name: device.name,
            isSelected: false,
          };
        });
      });
    },
    onDeleteSelected() {},
    onCreateDialogOpen() {},
    selectNetwork() {},
    getDeviceIdFromKey(groupKey) {
      return groupKey.split(DELIMITER)[1];
    },
    getNetworkIdFromKey(groupKey) {
      return groupKey.split(DELIMITER)[0];
    },
    onDeviceSelection(groupKey, index) {
      const isSelected = this.networkDevicesModel[groupKey].isSelected;

      const networkId = this.getNetworkIdFromKey(groupKey);
      const deviceId = this.getDeviceIdFromKey(groupKey);
      if (isSelected) {
        this.networks.forEach(network => {
          const points = network.items
            .map(item => (item.device_uuid === deviceId ? { ...item } : null))
            .filter(val => val);
          this.selectedItems[networkId] = [...this.selectedItems[networkId], ...points];
        });
      } else {
        this.selectedItems[networkId] = this.selectedItems[networkId].filter(val => val.device_uuid !== deviceId);
      }
    },
    onSelectAll({ value }, selectedNetworkId) {
      Object.keys(this.networkDevicesModel).forEach(networkDeviceKey => {
        const networkId = this.getNetworkIdFromKey(networkDeviceKey);
        if (selectedNetworkId == networkId) {
          this.networkDevicesModel[networkDeviceKey].isSelected = value;
        }
      });
    },
    getTableHeaders(name, key, item, level = 0) {
      return {
        name,
        key: key,
        uuid: item.uuid,
        level: level,
        selected: false,
        isHeader: true,
      };
    },
    redirectToDevice(viewName, groupKey) {
      const networkId = this.getNetworkIdFromKey(groupKey);
      this.redirectTo(viewName, networkId);
    },
    redirectTo(viewName, uuid, deviceUUID) {
      if (!uuid) {
        return;
      }
      let destination = redirectMap[viewName];
      if (typeof destination === 'function') {
        this.$router.push({ path: destination(uuid, deviceUUID) });
      }
    },
    fetchSetGridHeaders() {
      gridTableHeadersService
        .fetch(this.tableName)
        .then(gridConfig => {
          const gridHeaders = headerBuilderService.gridHeadersResolver(this.tableName, gridConfig);
          this.headers = headerBuilderService.buildHeaders()(gridHeaders);
        })
        .catch(err => {
          this.headers = gridNames.DEFAULT_HEADERS[this.tableName];
        });
    },
  },
};
</script>
<style>
.network-group-wrapper {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
</style>
